<template>
  <div>
    <div class="flex justify-end">
      <button
        class="px-4 py-2 bg-purple-500 text-white font-semibold rounded"
        @click="showAddModal = true"
      >
        + Add
      </button>
    </div>
    <div v-if="properties" class="mt-4">
      <div v-if="properties.length > 0">
        <property-card
          v-for="property in properties"
          :key="property.id"
          class="mt-4"
          :property="property"
        />
      </div>
      <div v-else class="px-4 py-4 text-center bg-gray-200 rounded-lg">
        <p class="text-gray-600">
          No properties were found
        </p>
      </div>
    </div>
    <div v-else />
    <form-modal :open.sync="showAddModal" :form="form" title="Add Property" @input="onAdd" />
  </div>
</template>

<script>
import api from '../../mixins/api';
import PropertyForm from '../../components/forms/properties/PropertyForm';
import FormModal from '../../components/modals/FormModal';
import PropertyCard from '../../components/cards/properties/PropertyCard';

export default {
  components: {
    FormModal,
    PropertyCard,
  },
  data: () => ({
    properties: null,
    form: PropertyForm,
    showAddModal: false,
  }),
  async mounted() {
    try {
      const { data } = await api.getProperties();
      this.properties = data;
    } catch (error) {
      console.log(error);
    }
    try {
      const { data } = await api.getCodes();
      const values = data.map((item) => ({ value: item.id, name: `${item.name} (${item.code})` }));
      this.$store.commit('UPDATE_CODE_OPTIONS', values);
    } catch (error) {
      console.log(error);
    }
  },
  methods: {
    async onAdd(form) {
      try {
        const { data } = await api.createProperty(form);
        if (this.properties) {
          this.properties.push(data);
        } else {
          this.properties = [data];
        }
      } catch (error) {
        console.log(error);
      }
    },
    async onUpdate(id, form) {
      try {
        const property = await api.updateProperty(id, form);
      } catch (error) {
        console.log(error);
      }
    },
    async onDelete(id) {
      try {
        await api.deleteProperty(id);
      } catch (error) {
        console.log(error);
      }
    },
  },
  head() {
    return {
      title: 'Properties',
    };
  },
};
</script>
